import alt from '../common/alt';
import SizeActions from '../actions/SizeActions';
import { message } from 'antd';
import Util from '../common/Util';

class SizeStore {
    constructor() {
        this.bindActions(SizeActions);
        this.state = {
            dataSource: [],
            isLoad: false,
            sizeName: '',
            sizes: [] // for filter page to load all sizes
        }
    }

    createDataSource(store) {
        return store.map((item, index) => {
            return {
                key: index,
                id: {
                    editable: false,
                    value: item.id,
                    changeable: false
                },
                sizeName: {
                    editable: false,
                    value: item.sizeName,
                    changeable: true
                },
                createTime: {
                    editable: false,
                    value: item.createTime,
                    changeable: false
                }
            }
        });
    }

    onGetAllSizesSuccess(data) {
        this.setState({
            sizes: data,
            dataSource: this.createDataSource(data),
            isLoad: true
        });
    }

    onUpdateSizeSuccess(data) {
        message.info(data.isCancel ? '.' : '. ');
        this.setState({ dataSource : data.dataSource });
    }

    onUpdateSizeFail(data) {
        message.error(': ' + data);
        setTimeout(function() {
            Util.changLocation("/zhijian/sizes")
        }, 500);
    }

    onUpdateSizeName(event) {
        this.setState({
            sizeName: event.target.value
        });
    }

    onAddSizeSuccess(data) {
        message.info(': ' + data.sizeName);
        let dataSource = [...this.state.dataSource];
        let newSize = {
            key: dataSource.length,
            id: {
                editable: false,
                value: data.id,
                changeable: false
            },
            sizeName: {
                editable: false,
                value: data.sizeName,
                changeable: true
            },
            createTime: {
                editable: false,
                value: data.createTime,
                changeable: false
            }
        };
        this.setState({
            dataSource: [...dataSource, newSize]
        });
    }

    onAddSizeFail(data) {
        message.error(data);
    }

    onDeleteSizeSuccess(data) {
        message.info('. ');
        const dataSource = [...this.state.dataSource];
        dataSource.splice(data.index, 1);
        this.setState({ dataSource });

    }

    onDeleteSizeFail(data) {
        message.error(': ' + data);
    }
}

export default alt.createStore(SizeStore);


