import React from 'react';
import { Link } from 'react-router';
import { Layout, Menu, Icon } from 'antd';

const Sider = Layout.Sider;
const SubMenu = Menu.SubMenu;
const MenuItem = Menu.Item;
const menuUrls = [
    {
        key: "1",
        url: "/zhijian/tmpProducts"
    }, {
        key: "2",
        url: "/zhijian/formalProducts"
    }, {
        key: "3",
        url: "/zhijian/formalRemit"
    }, {
        key: "4",
        url: "/zhijian/tortProducts"
    }, {
        key: "5",
        url: "/zhijian/tortRemit"
    }, {
        key: "6",
        url: "/zhijian/tortWords"
    }, {
        key: "7",
        url: "/zhijian/categories"
    }, {
        key: "8",
        url: "/zhijian/sizes"
    }, {
        key: "9",
        url: "/zhijian/colours"
    }, {
        key: "10",
        url: "/zhijian/filters"
    }, {
        key: "11",
        url: "/zhijian/users"
    }, {
        key: "12",
        url: "/zhijian/addUser"
    }, {
        key: "13",
        url: "/zhijian/updateUser"
    }, {
        key: "14",
        url: "/zhijian/companies"
    },
];

const subMenus = [
    {
        key: "sub1",
        value: ["2", "3"]
    },{
        key: "sub2",
        value: ["4", "5"]
    },{
        key: "sub3",
        value: ["12", "13", "14", "15"]
    }
];
const isValid = localStorage.getItem("loginRole")!='admin';

class SiderPage extends React.Component {
    constructor(props) {
        super(props);
    }

    getSelectedMenu() {
        let currentLocation = location.href;
        let selected = menuUrls.filter((item) => {
            return currentLocation.indexOf(item.url) > -1;
        });
        return selected[0].key;
    }

    getOpenedSub(selectedKey) {
        let opened = subMenus.filter((item) => {
            return item.value.indexOf(selectedKey) > -1;
        });
        return opened[0] && opened[0].key;
    }

    render() {
        let updateUserPath = "/zhijian/updateUser/" + this.props.loginUserId;
        let selectedKey = this.getSelectedMenu();
        let openedSub = this.getOpenedSub(selectedKey);
        return (
                <Sider>
                    <Menu mode="inline" defaultSelectedKeys={[selectedKey]} defaultOpenKeys={[openedSub]}>
                        <MenuItem key="1">
                            <Icon type="switcher" />
                            <span className="nav-text"><Link to="/zhijian/tmpProducts"></Link></span>
                        </MenuItem>
                        <SubMenu key="sub1" title={<span><Icon type="database" /><span className="nav-text"><Link to="/zhijian/formalProducts"></Link></span></span>}>
                            <MenuItem key="2">
                                <Icon type="bars" />
                                <span className="nav-text"><Link to="/zhijian/formalProducts"></Link></span>
                            </MenuItem>
                        </SubMenu>
                        <SubMenu key="sub2" title={<span><Icon type="exception" /><span className="nav-text"><Link to="/zhijian/tortProducts"></Link></span></span>}>
                            <MenuItem key="4">
                                <Icon type="bars" />
                                <span className="nav-text"><Link to="/zhijian/tortProducts"></Link></span>
                            </MenuItem>
                        </SubMenu>
                        <MenuItem key="6">
                            <Icon type="bars" />
                            <span className="nav-text"><Link to="/zhijian/tortWords"></Link></span>
                        </MenuItem>
                        <MenuItem key="7">
                            <Icon type="tag-o" />
                            <span className="nav-text"><Link to="/zhijian/categories"></Link></span>
                        </MenuItem>
                        <MenuItem key="8">
                            <Icon type="scan" />
                            <span className="nav-text"><Link to="/zhijian/sizes"></Link></span>
                        </MenuItem>
                        <MenuItem key="9">
                            <Icon type="chrome" />
                            <span className="nav-text"><Link to="/zhijian/colours"></Link></span>
                        </MenuItem>
                        <MenuItem key="10">
                            <Icon type="filter" />
                            <span className="nav-text"><Link to="/zhijian/filters"></Link></span>
                        </MenuItem>
                        <SubMenu key="sub3" title={<span><Icon type="share-alt" /><span className="nav-text"></span></span>} disabled={isValid}>
                            <MenuItem key="11">
                                <Icon type="user" />
                                <span className="nav-text"><Link to="/zhijian/users"></Link></span>
                            </MenuItem>
                            <MenuItem key="12">
                                <Icon type="user-add" />
                                <span className="nav-text"><Link to="/zhijian/addUser"></Link></span>
                            </MenuItem>
                            <MenuItem key="13">
                                <Icon type="setting" />
                                <span className="nav-text"><Link to={updateUserPath}></Link></span>
                            </MenuItem>
                            <MenuItem key="14">
                                <Icon type="team" />
                                <span className="nav-text"><Link to="/zhijian/companies"></Link></span>
                            </MenuItem>
                        </SubMenu>
                    </Menu>
                </Sider>
        );
    }
}

export default SiderPage;


